import React, { Component } from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import enFAQ from '../assets/enFAQ';
import plFAQ from '../assets/plFAQ';
import detectBrowserLanguage from 'detect-browser-language';

class FAQ extends Component {
  constructor() {
    super();
    this.state = {
      FAQfile: enFAQ,
    };
  }

  componentDidMount() {
    const lang = detectBrowserLanguage();
    if (lang === 'pl-PL' || lang === 'pl') this.setState({ FAQfile: plFAQ });
    else this.setState({ FAQfile: enFAQ });
  }
  render() {
    return (
      <StyledFAQ>
        <Header lang={detectBrowserLanguage()} />
        <FAQContainer>
          {this.state.FAQfile.map((item) => (
            <FAQInner>
              <Question>{item.Q}</Question>
              <Answer>{item.A}</Answer>
            </FAQInner>
          ))}

          {detectBrowserLanguage() === 'pl-PL' ||
          detectBrowserLanguage() === 'pl' ? (
            <Answer>
              1. Po otwarciu aplikacji, uytkownik wybiera jzyk, w ktrym
              bdzie rozmawia z innymi.
              <br />
              <br />
              2. Nastpnie, uytkownik wybiera swoje zainteresowanie spord
              przedstawionych mu kategorii, w ktrych chce rozmawia.
              <br />
              <br />
              3. Uytkownik jest przekierowywany do okna takiego jak w Snapchat,
              widzi tylko obraz ze swojej kamery, wraz z opcjami takimi jak
              wyszukiwanie nowych osb lub kopiowanie linku, ktry odpowiada za
              dostp do obecnego kanau. Ostatni, lecz rwnie wan funkcj
              jest opcja zmiany preferencji jzyka i kategorii, bez
              niepotrzebnego resetowania aplikacji.
              <br />
              <br />
              4. W obecnym oknie, uytkownik moe rozpocz szukanie kanaw z
              losowymi osobami lub zaprasza swoich przyjaci do pokoju, w celu
              wsplnego dowiadczania wrae.
              <br />
              <br />
              5. Dopiero teraz rozpoczyna si prawdziwa przygoda, poniewa
              rozmowa jeden na jednego nie jest tak ekscytujca, jak rozmowa w
              kilka osb jednoczenie. W tym momencie do uytkownika dochodzi
              kilka funkcji, takich jak np. moliwo przeczenia si na czat
              tekstowy, wyciszenia mikrofonu, wygaszenia kamery, opuszczenia
              obecnego kanau lub te wyszukania nastpnych pokoi. Opcja
              natychmiastowego opuszczania kanau jest oczywista i kieruje ona
              do okna, w ktrym widzimy tylko swoj kamer. Przycisk
              wyszukiwania nastpnych osb pozwala na szukanie potencjalnych
              uytkownikw, obracajcych si w krgu naszych zainteresowa.
              Wszystkie opcje wyej wymienione zostaj cay czas z
              uytkownikiem, bez wzgldu na to ile osb rozmawia aktualnie w
              pokoju.
              <br />
              <br />
              6. Powiedzmy, e rozmawiamy z czterema osobami i chcemy zabra
              gos w jakiej wanej sprawie, tym samym nie przerywajc nikomu
              dyskusji. Moemy w tym samym momencie skorzysta z oznaczenia
              danego rozmwcy, korzystajc z czatu tekstowego wpisujc jego
              kolor, a nastpnie wysa do niego wiadomo. Nasz serwis stawia
              na anonimowo., wic uytkownik nie musi zakada adnych kont,
              wyjtek stanowi usuga Premium. Z nastpn funkcj uytkownik ma
              styczno wtedy kiedy chce doj do kanau, na ktrym rozmawia co
              najmniej dwch uytkownikw. Uytkownik ten jest domylnie
              wyciszany na 10 sekund, poniewa mgby on w tym czasie zacz
              przeszkadza innym rozmwcom. Jeeli uytkownik okae si by na
              tyle zoliwy, e zacznie sprawia problemy na kanale, na zasadzie
              demokratycznego gosowania grupa moe wyrzuci go z pokoju, bd
              zgosi za naruszenia. Dostarczamy rwnie takich opcji jak
              wyciszanie i wyczanie kamery konkretnego uytkownika.{' '}
            </Answer>
          ) : (
            <Answer>
              1. After opening our app, the user chooses the language he/ she
              wishes to talk to others.
              <br />
              <br />
              2. Next, the user picks the category he/ she wants to talk about.
              <br />
              <br />
              3. User is redirected to a view that just like on snapchat shows
              only his camera view with options to search for a lobby and copy a
              share link. The last but not least option is a settings panel that
              lets you change language and category preferences without
              restarting the app.
              <br />
              <br />
              4. From that point the user can search for a lobby with random
              people or invite a friend and continue the experience together.
              <br />
              <br />
              5. Now the journey really begins but the conversation one on one
              is not as exciting. Some options come into play though and the
              user now has a text chat, camera, microphone exclusion and leave
              now or search for new lobby options. The leave now option is
              obvious enough but the search for a new lobby button lets you look
              for new potential people without disconnecting current ones. All
              of the above options stay for the rest no matter the amount of
              people in the lobby.
              <br />
              <br />
              6. Lets say youre talking with 4 people and we got a few more
              interesting points wed like to share with you. First if you want
              to make a point to someone and not interrupt the conversation you
              can use a chat and tag someone by its color as its prefered
              recognition system, we provide some anonymity and there is no
              account creation, except premium accounts in our flow. The next
              one is that user that is joining a lobby of at least two people is
              by default muted for 10s not to spam, shout and interrupt others.
              If the user ends up malicious he can be kicked by a vote of the
              rest and as well reported. We provide options such as muting the
              specific person or hiding his camera view.
            </Answer>
          )}
        </FAQContainer>
      </StyledFAQ>
    );
  }
}

export default FAQ;

const StyledFAQ = styled.div`
  width: 100vw;
  margin-bottom: 200px;
`;
const FAQContainer = styled.div`
  margin: 0 auto;
  margin-top: 100px;
  width: 90%;
  display: flex;
  flex-direction: column;

  @media (min-width: 500px) {
    width: 400px;
  }

  @media (min-width: 800px) {
    width: 700px;
  }
`;
const FAQInner = styled.div`
  margin-bottom: 20px;
`;
const Question = styled.p`
  font-weight: 700;
  font-size: 1.6rem;
`;
const Answer = styled.p`
  margin: 0;
`;
